import React from 'react';
import styled from 'styled-components';


class EmailForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {
    alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Name:
          <input type="text" value={this.state.value} onChange={this.handleChange} />
        </label>
        <label>
          Email:
          <input type="text" value={this.state.value} onChange={this.handleChange} />
        </label>

      <Call2Button accent="inverse">
        <a href="google.com"target="_blank">
          <input type="submit" value="Submit" />
        </a>
      </Call2Button>

        
      </form>
    );
  }
}


const Call2Button = styled.div`
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  border-radius: 100px;
  border: 2px solid;
  border-color: ${props => props.theme.color.grayscale.S1000};
  line-height: 24px;
  padding: 11px 25px;
  margin: 35px 0;
  -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  ${props =>
    props.accent &&
    `border-color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
  }}`};

  a {
    text-decoration: none;
    color: ${props => props.theme.color.grayscale.S1000};
    -webkit-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.white.regular
          : props.theme.color.grayscale.S1000
    }}`};
  } 
  a:hover{
    text-decoration: none !important;
  }
  div {
    display:inline-block;
    margin-right: 5px;
    -webkit-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
  }
  &:hover {
    a{
      color: ${props => props.theme.color.secondary.S400};
    }
    div {
      margin-right: 15px;
    }
    border-color: ${props => props.theme.color.secondary.S400};
  }

  
`
;

export default EmailForm;
